import uniPopup from "@/components/uni-popup/uni-popup.vue";
import uniTag from "@/components/uni-tag/uni-tag.vue";
import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue';
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue';
import {
	invoiceQueryInvoices,
	invoiceDelInvoice
} from '@/service/service.js';
import {
	InvoiceTypes,
	FmtInvoiceVerifyStatus,
	ExtendProps,
	FmtReimburseStatus,
	FmtReimburseColorStatus
} from '@/common/spb.js';
import {
	ActionPzsp
} from '@/utils/actions.js';
export default {
	components: {
		uniSwipeAction,
		uniSwipeActionItem,
		uniPopup,
		uniTag
	},
	data() {
		let mockInvoiceData = {
			amount: 150.0,
			taxAmount: 15.0,
			invoiceQuantity: '5',
			allTicketList: [{
				time: '2019-01',
				count: 3,
				sumAmount: 60.0,
				items: [{
						id: 1,
						invoiceType: 'SpecInvoice',
						amount: 10.0,
						taxAmount: 1.0,
						invoiceTime: '2019-01-01',
						companyName: '北京多啦科技有限公司',
						departure: '',
						destination: '',
						city: '',
						checkState: '0',
						checked: false,
						disabled: false
					},
					{
						id: 2,
						invoiceType: 'PlaneInvoice',
						amount: 20.0,
						taxAmount: 2.0,
						invoiceTime: '2019-01-01',
						companyName: '',
						departure: '沧州西',
						destination: '北京南',
						city: '',
						checkState: '0',
						checked: false
					},
					{
						id: 3,
						invoiceType: 'QuotaInvoice',
						amount: 30.0,
						taxAmount: 3.0,
						invoiceTime: '2019-01-01',
						companyName: '',
						departure: '沧州西',
						destination: '北京南',
						city: '北京',
						checkState: '0',
						checked: false,
						disabled: false
					}
				]
			}, {
				time: '2019-02',
				count: 2,
				sumAmount: 90.0,
				items: [{
						id: 4,
						invoiceType: 'SpecInvoice',
						amount: 40.0,
						taxAmount: 4.0,
						invoiceTime: '2019-01-01',
						companyName: '北京多啦科技有限公司',
						departure: '',
						destination: '',
						city: '',
						checkState: '0',
						checked: false,
						disabled: false
					},
					{
						id: 5,
						invoiceType: 'PlaneInvoice',
						amount: 50.0,
						taxAmount: 5.0,
						invoiceTime: '2019-01-01',
						companyName: '',
						departure: '沧州西',
						destination: '北京南',
						city: '',
						checkState: '0',
						checked: false,
						disabled: false
					}
				]
			}]
		};
		let deftInvoiceData = {
			amount: 0,
			taxAmount: 0,
			invoiceQuantity: '0',
			allTicketList: []
		};
		let itypes = ExtendProps(InvoiceTypes, {
			slted: false,
			ttype: 'default'
		});
		return {
			biz: 'GL', //GL:管理 XZ:选择
			filter: {
				invoiceStartTime: "",
				invoiceEndTime: "",
				selectReimburse: false
			},
			invoiceTypes: itypes,
			invoiceData: deftInvoiceData,
			swipeOptions: [{
				text: '删除',
				style: {
					backgroundColor: '#DD524D'
				}
			}],
			chosenInvoice: {
				items: [],
				count: 0,
				amount: 0
			}
		};
	},
	computed: {
		datanone: function() {
			return (this.invoiceData.allTicketList.length < 1);
		}
	},
	onLoad(option) {
		if (option != undefined) {
			this.biz = option.biz;
		}
		if (this.biz == 'XZ') {
			uni.hideTabBar({});
			this.synchronizeInvoiceChecked();
		}
		this.queryInvoices();
	},
	// 下拉刷新列表
	onPullDownRefresh() {
		console.log('下拉刷新发票列表..');
		this.queryInvoices(function() {
			uni.stopPullDownRefresh();
		});
	},
	methods: {
		handleStartTime(event) {
			this.filter.invoiceStartTime = event.detail.value;
			console.log("开始时间：" + this.filter.invoiceStartTime)
		},
		handleEndTime(event) {
			this.filter.invoiceEndTime = event.detail.value;
			console.log("结束时间：" + this.filter.invoiceEndTime)
		},
		// 弹出筛选窗口
		handleChooseFilter() {
			this.$refs.filter.open();
		},
		// 筛选条件显隐
		handleFilterPopupChange(e) {
			let args = {
				animation: true
			};
			if (e.show) {
				uni.hideTabBar(args);
			} else {
				uni.showTabBar(args);
			}
		},
		// 重置过滤条件
		handleFilterReset() {
			console.log('重置过滤条件..');
			this.filter = {
				invoiceStartTime: "",
				invoiceEndTime: "",
				selectReimburse: false
			};
			this.invoiceTypes.forEach((item, idx) => {
				if (item.slted) {
					this.handleFilterInvoiceTypeTag(idx);
				}
			});
		},
		// 确认过滤查询
		handleFilterQuery() {
			console.log('确认过滤查询..');
			this.queryInvoices();
			this.$refs.filter.close();
		},
		// 过滤发票类型
		handleFilterInvoiceTypeTag(idx) {
			console.log('选择过滤发票类型标签..', idx);
			let itype = this.invoiceTypes[idx];
			itype.slted = !itype.slted;
			itype.ttype = itype.slted ? 'primary' : 'default';
		},
		// 处理发票热区
		handleInvoiceHotspot(e) {
			console.log('处理发票热区点击事件..', e);
			let ids = e.currentTarget.id.split(',');
			let gidx = ids[0];
			let iidx = ids[1];
			let iid = ids[2];
			if (this.biz == 'XZ') {
				let invoice = this.invoiceData.allTicketList[gidx].items[iidx];
				if (invoice.disabled) return;
				console.log('处理发票热区点击事件,选择或取消选中发票..');
				let checked = invoice.checked;
				invoice.checked = !checked;
				if (invoice.checked) {
					this.addChosenInvoice(invoice);
				} else {
					this.delChosenInvoice(invoice);
				}
				this.computeChosenInvoice();
				return;
			}
			console.log('处理发票热区点击事件,跳转到发票详情页面..');
			let state = encodeURIComponent(JSON.stringify({
				id: iid
			}));
			uni.navigateTo({
				url: `../../ticket/detail/detail_tt?biz=fpgl&edit=false&state=${state}`
			});
		},
		// 处理左滑删除
		handleSwipeClick(e, iid) {
			let that = this;
			let action = e.content.text;
			switch (action) {
				case '删除':
					uni.showModal({
						title: '',
						content: '您确定删除此发票吗？',
						success: function(res) {
							if (res.confirm) {
								console.log('删除发票-确定', iid);
								let arg = {
									invoiceId: iid
								};
								invoiceDelInvoice(arg).then((diRes) => {
									console.log('删除指定发票..', diRes);
									if (diRes && diRes.success) {
										that.queryInvoices();
									}
								});

							} else if (res.cancel) {
								console.log('删除发票-取消', iid);
							}
						}
					});
					break;
			}
		},
		// 处理拍照收票
		handlePzspAction() {
			ActionPzsp();
		},
		// 添加选择的发票
		addChosenInvoice(invoice) {
			let items = this.chosenInvoice.items;
			items.push(invoice);
		},
		// 删除选择的发票
		delChosenInvoice(invoice) {
			let id = invoice.id;
			let idx = -1;
			let items = this.chosenInvoice.items;
			items.forEach((item, index) => {
				if (item.id == id) {
					idx = index;
					return;
				}
			});
			if (idx != -1) {
				items.splice(idx, 1);
			}
		},
		// 计算选中的发票
		computeChosenInvoice() {
			let ci = this.chosenInvoice;
			let items = ci.items;
			let count = 0;
			let amount = 0;
			items.forEach((item) => {
				count = count + 1;
				amount = amount + item.amount
			});
			ci.count = count;
			ci.amount = amount;
		},
		// 处理已选发票确定
		handleChooseInvoicesSubmit() {
			let rmb = store.state.reimburse;
			let aci = rmb.allChosenInvoice;
			let cci = rmb.crtChosenInvoice;
			cci = [];
			this.chosenInvoice.items.forEach((item) => {
				aci.push({
					fpId: item.id
				});
				cci.push({
					fpId: item.id,
					fplx: this.fmtInvoiceType(item.invoiceType),
					kprq: item.invoiceTime,
					je: item.amount
				});
			});
			uni.$emit('ReimburseChooseInvoices', {
				msg: 'OK'
			});
			uni.navigateBack({
				delta: 1
			});
		},
		// 同步发票选中状态
		synchronizeInvoiceChecked() {
			let rmb = store.state.reimburse;
			let aci = rmb.allChosenInvoice;
			let cci = rmb.crtChosenInvoice;
			let cis = this.chosenInvoice.items;
			let atl = this.invoiceData.allTicketList;
			console.log(111+atl)
			cis = [];
			atl.forEach((item) => {
				item.items.forEach((invoice) => {
					invoice.checked = false;
					invoice.disabled = true;
					let aciExisted = (aci.find((aciItem) => {
						return (aciItem.fpId == invoice.id);
					}) != null);
					let cciExisted = (cci.find((cciItem) => {
						return (cciItem.fpId == invoice.id);
					}) != null);
					if (aciExisted) {
						invoice.checked = true;
						cis.push(invoice);
					}
					if (cciExisted || !aciExisted) invoice.disabled = false;
				});
			});
			this.computeChosenInvoice();
		},
		// 查询发票列表
		queryInvoices(callback) {
			let arg = this.filter;
			let types = [];
			this.invoiceTypes.forEach((item) => {
				if (item.slted) {
					types.push(item.value);
				}
			});
			arg.invoiceTypes = types.join(',');
			invoiceQueryInvoices(arg).then((qiRes) => {
				console.log('查询发票列表..', qiRes);
				if (callback) callback();
				if (qiRes && qiRes.success) {
					this.invoiceData = qiRes.data.data;
				}
			});
		},
		// 是否显示销方
		isShowSaler(itype) {
			switch (itype) {
				case 'SpecInvoice':
				case 'VolInvoice':
				case 'EleInvoice':
				case 'GenerInvoice':
				case 'EleTollInvoice':
				case 'VehInvoice':
				case 'QuotaInvoice':
				case 'PlaneInvoice':
				case 'PassengerInvoice':
				case 'GenerMachineInvoice':
				case 'TaxiInvoice':
				case 'TollInvoice':
				case 'TrainInvoice':
					return true;
				default:
					return false;
			}
		},
		// 是否显示城市
		isShowCity(itype) {
			switch (itype) {
				case 'QuotaInvoice':
				case 'TaxiInvoice':
					return true;
				default:
					return false;
			}
		},
		// 是否显示行程
		isShowJourney(itype) {
			switch (itype) {
				case 'PlaneInvoice':
				case 'PassengerInvoice':
				case 'TrainInvoice':
					return true;
				default:
					return false;
			}
		},
		// 格式化发票类型
		fmtInvoiceType(itype) {
			let its = this.invoiceTypes;
			let c = its.length;
			for (let i = 0; i < c; i++) {
				let t = its[i];
				if (t.value == itype) {
					return t.label;
				}
			}
			return '无效类型';
		},
		// 格式化发票查验状态
		fmtInvoiceVerifyStatus(istate) {
			return FmtInvoiceVerifyStatus(istate);
		},
		//格式化发票报销状态
		fmtReimburseStatus(istatus) {
			const colorStatus = FmtReimburseStatus(istatus);
			console.log(colorStatus.label)
			// if(colorStatus.label == undefined) {
			// 	colorStatus.label = '无';
			// }
			return colorStatus.label;
		}
	}
}
